
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,AlertController,LoadingController, ToastController, } from 'ionic-angular';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Events } from 'ionic-angular';
import { Storage } from '@ionic/storage';


/**
 * Generated class for the HLoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-h-login',
  templateUrl: 'h-login.html',
})
export class HLoginPage {

  email:any;
  pass:any;
  hisorylist:any;
   sno:any;

  constructor(public storage:Storage, public events: Events,public navCtrl: NavController, public forgotCtrl: AlertController,public navParams: NavParams,private loadingCtrl: LoadingController,private http:Http,public toastCtrl: ToastController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad HLoginPage');
  }

  onSignup() {
    this.navCtrl.push('HsignupPage');
  }

  onLogin() {
    this.navCtrl.push('HdashPage');
  }

  register() {
    this.navCtrl.setRoot('HsignupPage');
  }

  // login and go to home page
  login() {
    this.navCtrl.setRoot('HdashPage');
  }
  private presentToast(text) {
    let toast = this.toastCtrl.create({
      message: text,
      duration: 5000,
      position: 'bottom'
    });
    toast.present();
  }
 
  submitForm() {
   // let url = "http://sputnikinfotech.com/nms/index.php/api/nms_profile";


    //let url = "http://sputnikinfotech.com/nms/index.php/api/nmsregister";

    let url = "http://sputnikinfotech.com/nms/index.php/api/loginn";
    console.log(url);
    let loading = this.loadingCtrl.create({
    });
    loading.present();
    let body = JSON.stringify({ appkey: "t@ftc2o18", username: this.email, password: this.pass });
    let headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded' });
    let options = new RequestOptions({ headers: headers });
    //let photo = '';
    this.http
      .post(url, body, options)
      .subscribe(
        data => {
          loading.dismiss();
          let dataval = data.json();
         /*   console.log(dataval['status']);
           console.log(dataval.status); */
           console.log(dataval.status)
           console.log(dataval)

          if (dataval.status== 'error') {
            this.presentToast('Enter correct Username and Password');
          }
          else if (dataval.status== '1') {
            this.presentToast('Please Contact Admin');
          }
          
          else if(dataval.status=='success') {
            this.storage.set('nms', dataval[0].name);
            localStorage.setItem('name', dataval[0].name); 
            localStorage.setItem('phone', dataval[0].phone);   
  
            localStorage.setItem('userid', dataval[0].user_id);   

            this.events.publish('user:created',dataval[0].name, dataval[0].photo, Date.now());
            this.navCtrl.setRoot('HdashPage',{userid:dataval[0].user_id});     
            console.log("userid",dataval[0].user_id);          

          }
          else{
            this.presentToast('Enter correct Username & Password');

          }


        },
        err => {
          loading.dismiss();
          console.log("ERROR!: ", err);
        }
      );
  }

  forgotPass() {
    let forgot = this.forgotCtrl.create({
      title: 'Forgot Password?',
      message: "Enter your phone no to send a reset link password.",
      inputs: [
        {
          name: 'email',
          placeholder: 'phone no',
          type: 'email'
        },
      ],
      buttons: [
        {
          text: 'Cancel',
          handler: data => {
            console.log('Cancel clicked');
          }
        },
        {
          text: 'Send',
          handler: data => {
            console.log('Send clicked');
             let toast = this.toastCtrl.create({
             // this.sms.send('8668186969', 'Hello world!');
              message: 'Password was sended successfully in your Phone',
              duration: 3000,
              position: 'top',
              cssClass: 'dark-trans',
              closeButtonText: 'OK',
              showCloseButton: true
            });
            toast.present();
          }
        }
      ]
    });
    forgot.present();
  }


}



